
import { computed, defineComponent, ref } from "vue";
import { useStore } from "vuex";
import { ElMessage } from "element-plus";
import { apiUserLogin } from "@/API";
export default defineComponent({
  name: "Login",
  components: {},
  setup() {
    const store = useStore();
    const state = store.state;
    const isLogin = computed(() => state.isLogin);
    const formVal = ref(null);
    return {
      state,
      isLogin,
      store,
      formVal,
    };
  },
  data() {
    const logoPath = {
      Path: require("@/assets/picture/logo.png"),
    };
    return {
      form: { password: "", email: "" },
      logoPath,
    };
  },
  methods: {
    handleSubmit() {
      if (!(this.form.password === "" || this.form.email === ""))
        apiUserLogin({
          password: this.form.password,
          email: this.form.email,
        })
          .then((res) => {
            console.log(res);
            this.store.commit("changeisLogin", true);
            this.store.commit("changeToken", res.data.data.tokenData.token);
            this.$router.push("/catalog");
            sessionStorage.setItem("email", this.form.email);
            sessionStorage.setItem("password", this.form.password);
          })
          .catch(() => {
            ElMessage.error("帳號密碼有誤 !");
            this.form.email = "";
            this.form.password = "";
          });
      else
        ElMessage.warning({
          message: "帳號或密碼尚未輸入",
          type: "warning",
        });
    },
  },
});
